body{
  background-image: url(./Files/Images/code-bg-2.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


/* Entry Content Styles*/
.entry-content{
  background: rgba(255, 255, 255, .9);
}

.entry-content * * {
font-family:'Roboto Condensed';
color: #333;
 }

.entry-content h1,
.entry-content h2,
.entry-content h3,
.entry-content h4,
.entry-content h5,
.entry-content h6 {
    color:#000;
    font-weight:700;
    line-height:1.2em
}

.entry-content h1 { font-size: 2.25rem; }
.entry-content h2 { font-size: 1.75em; }
.entry-content h3 { font-size: 1.5em; }
.entry-content h4 { font-size: 1.25em; }
.entry-content h5 { font-size: .83em; }
.entry-content h6 { font-size: .75em; }

.entry-content h3,
.entry-content h4 {
margin-bottom:.8em;
}

.entry-content h5,
 .entry-content h6 {
margin-bottom:.4em;
line-height:1.2em
 }

 .entry-content hr {
clear:both;
margin:1.5em auto 1.625em;
border:none;
border-top:1px dotted #999;
width:95%
 }
 .entry-content p,
 .entry-content ul,
 .entry-content ol,
 .entry-content dl,
 .entry-content table,
 .entry-content pre,
 .entry-content fieldset,
 .entry-content address,
 .entry-content blockquote {
margin-bottom:1.625em;
 }
 .entry-content ul p,
 .entry-content ol p,
 .entry-content dl p,
 .entry-content blockquote p .entry-content fieldset p {
margin-bottom:1.2em
 }
 .entry-content ul {
list-style:square inside none
 }
 .entry-content ol {
list-style:decimal inside none
 }
 .entry-content ol ol {
list-style:upper-alpha inside none
 }
 .entry-content ol ol ol {
list-style:lower-roman inside none
 }
 .entry-content ol ol ol ol {
list-style:lower-alpha inside none
 }
 .entry-content ul ul,
 .entry-content ol ol,
 .entry-content ul ol,
 .entry-content ol ul {
margin-left:2.5em
 }
 .entry-content ul li+li,
 .entry-content ol li+li {
margin-top:.35em
 }
 .entry-content dl dl {
margin:0 0 0 1.5em
 }
 .entry-content dt {
font-weight:bold
 }
 .entry-content dd {
margin:0 0 0 1.1em
 }
 .entry-content dl dt+dt,
 .entry-content dl dd+dt,
 .entry-content dl dd+dd {
margin:.8em 0 0
 }
 .entry-content fieldset {
padding:1.5em;
border:1px solid #999
 }
 .entry-content fieldset legend {
padding:0 1.1em;
color:#dd1c27;
font-weight:bold
 }
 .entry-content blockquote,
 .entry-content blockquote * {
font-style:italic;
font-family:Georgia,serif
 }
 .entry-content blockquote {
border:1px dotted #999;
padding:1.1em;
background:#f9f9f9
 }
 .entry-content blockquote cite,
 .entry-content blockquote em,
 .entry-content blockquote i {
font-style:normal
 }
 .entry-content pre {
overflow:scroll;
border:1px dotted #999;
padding:1.1em;
background:#f9f9f9
 }
 .entry-content address {
font-style:italic
 }
 .entry-content a {
color:#0054a6;
text-decoration:none;
 }
 .entry-content a:hover,
 .entry-content a:focus,
 .entry-content a:active {
color:#999
 }
 .entry-content b,
 .entry-content strong {
font-weight:bold
 }
 .entry-content u {
text-decoration:underline
 }
 .entry-content i,
 .entry-content q,
 .entry-content cite,
 .entry-content em {
font-style:italic
 }
 .entry-content code,
 .entry-content kbd,
 .entry-content var,
 .entry-content samp {
font:1em/1.1em Courier New,monospace
 }
 .entry-content big {
font-size:1.25em
 }
 .entry-content small {
font-size:.75em
 }
 .entry-content sup,
 .entry-content sub {
position:relative;
height:0;
vertical-align:baseline;
font-size:.625em
 }
 .entry-content sup {
bottom:1ex
 }
 .entry-content sub {
top:.7ex
 }
 .entry-content abbr,
 .entry-content acronym,
 .entry-content dfn {
border-bottom:1px dotted #999;
cursor:help
 }
 .entry-content mark {
background:#ff9;
color:#333
 }
 .entry-content ins {
background:#d3f8d0;
color:#333;
text-decoration:none
 }
 .entry-content del {
background:#ffc2c2;
color:#333;
text-decoration:line-through
 }
 .entry-content s,
 .entry-content strike {
text-decoration:line-through
 }
 .entry-content img {
max-width:100%;
height:auto!important
 }
 .entry-content img.no-border {
border:0;
padding:0;
max-width:100%;
background:transparent!important
 }
 .entry-content img.left,
 .entry-content img.right,
 .entry-content img.center {
margin-bottom:1.625em
 }
 .entry-content a:hover img,
 .entry-content a:focus img,
 .entry-content a:active img {
cursor:pointer;
background:#f9f9f9
 }
 .entry-content figure {
display:block;
margin:.4em auto 1.8em;
border:1px solid #999;
padding:1em;
max-width:100%;
background:#fff;
text-align:center;
-webkit-box-sizing:border-box;
-moz-box-sizing:border-box;
box-sizing:border-box
 }
 .entry-content figure img {
padding:0
 }
 .entry-content figure figcaption {
display:block;
margin:.4em;
font:1em/1.2em Georgia,Serif;
text-align:left
 }
 .entry-content table {
border:0;
max-width:100%;
vertical-align:middle
 }
 .entry-content table th {
padding:.6em 1.5em;
background:#fff;
color:#999;
font-weight:bold
 }
 .entry-content table td {
padding:.6em 1.5em
 }
 .entry-content table tr+tr th,
 .entry-content table tr+tr td {
border-top:1px solid #999
 }
 .entry-content table th+th,
 .entry-content table th+td,
 .entry-content table td+td,
 .entry-content table td+th {
border-left:1px solid #999
 }
 .entry-content table.datatable {
border:1px solid #999;
width:100%;
text-align:left
 }
 .entry-content table.datatable th {
padding:.8em;
background:#fff;
color:#dd1c27;
font-weight:bold
 }
 .entry-content table.datatable td {
padding:.8em;
background:#f9f9f9
 }
 .entry-content table.datatable tr+tr th,
 .entry-content table.datatable tr+tr td {
border-top:1px solid #999
 }
 .entry-content table.datatable th+th,
 .entry-content table.datatable th+td,
 .entry-content table.datatable td+td,
 .entry-content table.datatable td+th {
border-left:1px solid #999
 }
 .entry-content .left {
display:inline;
float:left;
margin-right:1.625em
 }
 .entry-content .right {
display:inline;
float:right;
margin-left:1.625em
 }
 .entry-content .center {
display:block;
clear:both;
margin-left:auto;
margin-right:auto
 }
 .entry-content .clear,
 .entry-content .clear:after {
clear:both
 }
 .entry-content .text-left {
text-align:left
 }
 .entry-content .text-right {
text-align:right
 }
 .entry-content .text-center {
text-align:center
 }
 .entry-content .text-justify {
text-align:justify
 }
 .entry-content .align-left {
float:left;
margin-right:1.625em
 }
 .entry-content .align-right {
float:right;
margin-left:1.625em
 }
 .entry-content .align-center {
display:block;
clear:both;
margin-left:auto;
margin-right:auto;
text-align:center
 }
 .figcaption,
 .figure {
display:block;
margin-top:5px;
text-align:center
 }

.entry-content .spec-p{
padding-bottom: 30px;
font: 18px/1.5 Arial,sans-serif;
color: #3c3c3c;
}

.entry-content .btn{
text-decoration: none;
color: #fff;
}

.entry-content .btn:focus{
text-decoration: none;
color: #fff;
}


/*#region Skills Section*/
.skills-section img{
 padding-bottom: 15px; 
}

.skill-label{
 display: block;
 color: #fff;
 font: 700 14px 'Roboto-Condensed',Arial;
 text-shadow: 0px 0px 2px #000;
}
/*#endregion */

/*#region Skills Section Animation bar*/
.skills-section-bar img{
 max-width: 50%;
 padding-top: 12px; 
 padding-bottom: 12px; 
}

.skill-label{
 display: block;
 color: #fff;
 font: 700 18px 'Roboto-Condensed',Arial;
 text-shadow: 0px 0px 2px #000;
}

.skills-item{
 display: -webkit-box; 
 display: -moz-box;
 display: -ms-flexbox;
 display: -webkit-flex; 
 display: flex;
 background: #fff;
 margin-bottom: 20px;
 border-radius: 10px;
 overflow: hidden;
}

.skill-name{
 flex: 1;
 position: relative;
 z-index: 1;
}

.html-box{background: #f16529}
.css-box{background: #21aad7}
.js-box{background: #f0db4f}
.bootstrap-box{background: #5c3e84}

.skill-status-wrapper{
 flex: 10;
}

.skill-status{
 display: block;
 height: 100%;
 border-radius: 0 10px 10px 0; 
}

.skill-status-wrapper span
{
 position: absolute;
 right: 10px;
 top: 0;
 display: flex;
 justify-content: center;
 align-self: center;
 flex-direction: column;
 margin: auto;
 bottom: 0;
 font: 700 30px 'Roboto Condensed',Arial;
}

.w-10{width: 10%}
.w-20{width: 20%}
.w-30{width: 30%}
.w-40{width: 40%}
.w-50{width: 50%}
.w-60{width: 60%}
.w-70{width: 70%}
.w-80{width: 80%}
.w-90{width: 90%}
.w-100{width: 100%}

.skill-life-0{background: #ff0000;}
.skill-life-10{background:#ef1000;}
.skill-life-20{background:#d82700 ;}
.skill-life-30{background:#bd4200 ;}
.skill-life-40{background:#9f6000 ;}
.skill-life-50{background:#808000 ;}
.skill-life-60{background:#609f00 ;}
.skill-life-70{background:#42bd00 ;}
.skill-life-80{background:#27d800 ;}
.skill-life-90{background:#11ee00 ;}
.skill-life-100{background:#00ff00 ;}
/*#endregion */

/*#region  CV page*/

.cv-page {
 padding: 4rem;
}

.cv-header-name {
text-transform: uppercase;
}

.cv-header-meta {
border-left: 1px solid #e0e0e0;
}

.cv-section-content{
  margin-bottom: 1.5rem;
}


.cv-section-title {
margin-bottom: 15px;
page-break-after: avoid;
}


.tech-tags, .tech-tags>li {
 margin: 0;
 padding: 0;
}

.tech-tags {
 list-style: none;
}

.tech-tags li {
  padding: 0 2px;
 display: inline-block;
 overflow: hidden;
}

.cv-page .tech-tags a {
 display: inline-block;
 background-color: rgba(175,34,34,.8);
 transition: background-color 0.5s ease;
 color: #FFF;
 padding: 4px 10px;
 font-size: 13px;
 text-transform: uppercase;
}

.cv-page .tech-tags a:hover{
 color: #fff;
 background: #000;
}

.cv-timeline-position {
 margin-bottom: 10px;
 color: #333;
 line-height: 1.3;
}

/*#endregion */

.table-header, 
.table-row {
  display: flex; 
}

.table-col-1 {
  flex: 1;        /* distributes space on the line equally among items */
}



